// @ts-nocheck
import React, { createContext, useState, useEffect, useCallback } from 'react';

export interface IJwtContext {
    jwt: string;
    fetchJWT: () => void;
}

export const JwtContext = createContext<IJwtContext>({
    jwt: "",
    fetchJWT: () => { }
});
const fetchBase =
    process.env.NODE_ENV === 'development'
        ? 'http://localhost:8000'
        : 'https://peopleparty-server.herokuapp.com';

export const JwtProvider = ({ children }: { children: React.ReactNode }) => {
    const [jwt, setJwt] = useState("");



    const fetchJWT = useCallback(
        async (): Promise<IFetchResponseBase> => {
            const fetchRes = await fetch(fetchBase + `/users/login`, {
                method: 'GET'
            });

            if (fetchRes.ok) {
                let result = await fetchRes.json()
                setJwt(result._token);
                return result;
            }
        },
        [jwt]
    );

    useEffect(() => {
        fetchJWT();
    }, [fetchJWT, jwt]);



    return (
        <JwtContext.Provider
            value={{ jwt, fetchJWT }}
        >
            {children}
        </JwtContext.Provider>
    );
};